/*generic inputs*/
$inputs: 'input[type=text].pl-input, input[type=email].pl-input, input[type=password].pl-input, input[type=tel].pl-input, textarea.pl-input, .advance-form .select > select, button[type=button].pl-more-button';


@mixin reset-block-model {

	-webkit-appearance: none;

	-moz-appearance: none;

	appearance: none;
	background: none;
	border: none;
	border-radius: 0;
	box-sizing: border-box;
	display: block;
	line-height: 0;
	margin: 0;
	padding: 0;

}

/* General style & reset*/

#{$inputs} {

	@include reset-block-model;

	box-shadow: inset 0 0 0 1px $c-gray-lighter;
	font-size: 1.6rem;
	line-height: 26px;
	padding: 10px;
	position: relative;
	width: 100%;
	border-radius: 3px;

}

/* list-items*/

ul, ol {

	&.pl-list {
		list-style-type: none;
		padding: 0;

		li {
			margin-left: 0;
			overflow: hidden;
		}
		&--inline {

			@include clearfix;

			li {

				display: block;
				float: left;
				margin: 0;
				padding: 0;

				width: auto;

			}

			&--spaced li:not( :first-of-type ) { margin-left: 6em; }

			&--spaced li:nth-child(4) { margin-left: 0; }

			&--spaced--small li:not( :first-of-type ) { 
				margin-left: .5em; 
				@include media-breakpoint-up( xl ) {
					margin-left: .75em; 
				}
			}
			&--pipes li:not( :first-of-type ):before {

				content: '|';
				margin: 0 0.5em;

			}

		}
	}
}

/* Radio inputs and checkboxes*/

.pl-checkbox {
	display: inline-block;
	&:first-of-type {
		padding-bottom: 0;
	}
	.ac__contact-details & {
		&:last-of-type {
			margin-left: 25px;
			margin-right: 16px;
		}
	}

	input[type=checkbox], input[type=radio] { 
		/* display: none; */
	}

	label {

		display: block;
		font-size: 1.6rem;
		line-height: 1.9em; /* height of the checkbox*/
		margin: 0;
		padding-left: 1.5em;
		position: relative;
		overflow: visible;
		transition: all 0.15s ease-in;

		&:before,
		&:after {
			transition: all 0.15s ease-in;
			content: '';
			display: block;
			float: left;
			line-height: 0;
			margin-top: 0.3em;
			margin-left: -1.5em;
			margin-right: 1em;
			height: 18px; /* height of the checkbox*/
			width: 18px; /* height of the checkbox*/

		}

	}

	label:before {
		background: $c-white !important;
		box-shadow: inset 0 0 0 1px $c-gray-lighter;
		border-radius: 2px;
		/* margin-right: 0.5em;*/

	}
	
	label:after {
		visibility: hidden;

	}

	input:checked + label:after { visibility: visible; }

	input:checked + label:before { 

		background: $c-terracotta !important;

	}
	label:after {

		color: $c-white;
		content: '';
		background: url('../images/tick-white.png') no-repeat 3px 3px;
		float: none;
		position: absolute;
		top: 1px;
	}
}
.contact-form .styleSelBox.half-width {
	padding: 8px 60px 7px 15px;
	border: 1px solid $c-gray;
	-moz-appearance: none;

	width: 100%;
	background-position: 93% bottom;
	@include media-breakpoint-up( md ) {
		width: 280px;
	}
	.RTL &{
		background-position: 5% bottom;  
	}
}
.contact-form .error-container {
	float: initial;
	height: 20px;
}
input:disabled {
	opacity: 0.8;
}
select::-ms-expand {
    display: none;
}