@mixin custom-select($select-font-size: $size-large) {      
    $select-size: $select-font-size / ($select-font-size * 0 + 1);
    select {        
        color: $color-alizarin;            
        background: none;
        z-index: 0;
        -webkit-appearance:none;
        -moz-appearance: none;        
        border:none;    
        font-size: $select-font-size;      
        padding-right: #{$select-size}rem;
        display: none;
        position: absolute;
        bottom: 0;
        border-bottom: 0.2rem solid $color-alizarin;
        border-radius: 0;
        font-weight: 400;
    }
    @include media-only-mobile {

        select {
            display: inline-block;
            position: static;
        }
        &:after {
            content: '\e900';
            font-family: "GT-IconSet" !important;
            font-size: #{$select-size / 2}rem;
            color: $color-alizarin;
            font-weight: 700;
            pointer-events: none;
            display: block;
            position: absolute;
            right: 0;
            -ms-transform: translate(-50%,-95%);
            transform: translate(-50%,-95%);        
            
            
        }
    }
}
@mixin custom-name($name-font-size: $size-large) {      
    $name-size: $name-font-size / ($name-font-size * 0 + 1);
    .custom-name {
        display: inline-block;
        text-decoration: none;
        position: relative;
        z-index: 1;        
        color: $color-alizarin;            
        background: $color-warm-grey;
        -webkit-appearance:none;
        -moz-appearance: none;        
        border:none;     
        /*font-size: $name-font-size;      */
        padding-right: #{$name-size}rem;
        border-bottom: 0.2rem solid $color-alizarin;
        border-radius: 0;
        font-weight: 400;
        &:before {
            /*content: '\00a0';
            display: inline-block;
            font-size: $name-font-size;*/
        }
        &:after {
            content: '\e900';
            font-family: "GT-IconSet" !important;
            font-size: #{$name-size / 2}rem;
            color: $color-alizarin;
            font-weight: 700;
            pointer-events: none;
            display: block;
            position: absolute;
            right: 0;
            -ms-transform: translate(-50%,-95%);
            transform: translate(-50%,-95%);        
            
            
        }
        @include media-only-mobile {
            display:none;
            &:after {
                content: '';
                display: none;
            }
        }
    }
}
@mixin form-select($select-font-size: $size-tiny) {    
    position: relative;
    display: inline-block;
    /*margin-top: -0.75 * $margin-default;*/
    select {        
        width: inherit;
        color: $color-minsk;            
        background: none;
        -webkit-appearance:none;
        -moz-appearance: none;                    
        font-size: $select-font-size;      
        padding: 1rem 4rem 1rem 2rem;        
        border: 0.2rem solid $color-minsk;
        border-radius: 0;
    }
    &:after {
        content: '';   
        display: inline-block;                     
        border-left: 4px solid transparent;
        border-right: 4px solid transparent;
        border-top: 7px solid $color-minsk;                
        pointer-events: none;          
        margin-left: -3rem;    
        position:relative;    
    }

    &--light {
        select {
            color: $color-light;
            border-color: transparent;
            
            option {
                color: $color-dark;
            }
        }
        
        &:after {
            border-top-color: $color-light;
        }
    }

    @include media-only-mobile {   
        max-width: 100%;         
        select {
            max-width: inherit;            
        }
        &:after {
            position: absolute;
            margin-top: -3px;
            top: 50%;
            right: 2rem;
        }
    }
}
.custom-select {
    @include custom-select;
    position: relative;
    /*margin-right: 2rem;*/

    @include media-from-tablet {
        display: inline-block;
    }
}
