/* Right to left instructions
   ===============================================
   To test quickly, make sure that the HTML tag has the .RTL class AND the dir attribute
   has a 'rtl' value. You can also test against Israel pages if available.

   When testing, the dir attribute should always be set to 'rtl' as text direction is set
   automatically this way, and you don't want to add unnecessary styles.

   EXAMPLE (after the main .cookie-block styles in the _cookies.scss file):

	.RTL .cookie-block {
		p {
			color: cyan;
		}
	}

 	This would change the color of the text of the cookie block to cyan if the .RTL class
 	is set in the HTML tag.

 	The purpose of using the HTML level .RTL class is to keep right to left styles as
 	close as possible to their left to right counterparts so that if one changes the style
 	can be updated appropriately in the other, if necessary.
 */