.form-title {
    font-size: $size-large;
    display: block;
    padding-bottom: $padding-default / 2;    
    padding-left: 0;
    margin-top: $margin-default / 2;
}
.form-subtitle {
    font-size: $size-tiny;
    color: $color-alizarin;
    margin-top: -$margin-default  * 0.25;
    padding-bottom: $padding-default * 0.75;
}
%form-input, .form-input {
    display: block;
    border: 0.2rem solid $color-minsk;
    background: transparent;
    padding: 1rem 2rem;
    font-size: $size-tiny;
    color: $color-minsk;    
    margin-bottom: $margin-default/2;    
    max-width: 100%;
}
.form-select, .selCont {
    @include form-select;
    margin-bottom: $margin-default/2;
}
@include media-small-mobile {
    .form-input,.form-select {
        width: 100%;
    }
}
.form-button {
    display: block;
    padding: 1rem 2rem;
    margin-top: $margin-default * 3;
    span::after {
        padding: 1rem 2rem;
    }
}
input[type="text"] {
    @extend %form-input;
    display: inline-block;
}

input[type="file"], .file_limit_info {
    font-size: $size-regular;
    font-weight: 400;
}

label {
    color: $color-dark;
    @extend %p;
    padding-left: $padding-default;
    @include hover{
        cursor: pointer;
    }
}

input[type="checkbox"] {
    /* display: none;*/
    + label {
        position: relative;
        padding-left: 3rem;
        display: block;
        &:before {
            content: '';
            position: absolute;
            left: 0rem;
            width: 2rem;
            height: 2rem;
            border: 0.1rem solid $color-minsk;
        }

        &:after {
            content: '';
            opacity: 0;
            transition: opacity 0.2s ease-in;
        }
    }

    &:checked + label {
        &:after {
            content: '';    
            width: 1.4rem;
            height: 0.75rem;
            position: absolute;
            left: 3px;
            top: 0;
            margin-top: 0.4rem;
            border: 0.2rem solid $color-minsk;
            /*rtl:ignore*/
            border-top: none;
            /*rtl:ignore*/
            border-right: none;
            background: transparent;
            opacity: 1;            
            /*rtl:ignore*/
            -ms-transform: rotate(-55deg) skewX(-15deg);
            transform: rotate(-55deg) skewX(-15deg);
        }
    }    
}

input[type="radio"] {
    display: none;
    + label {
        display: block;
        position: relative;
        padding-left: 3rem;

         &:before {
            content: '';
            position: absolute;
            left: 0rem;
            margin-top: 0.6rem;
            width: 1rem;
            height: 1rem;
            border: 0.2rem solid $color-minsk;
            background: transparent;
            border-radius: 2rem;
        }
    }

      &:checked + label {
          &:before {
              background-color: $color-minsk;
              transition: background 0.2s ease-in;
          }
      }
}
textarea {
    @extend %form-input;
    display: inline-block;
    width: 80%;
}

input, textarea, select {    
    &.errors {
        border-color: $color-alizarin;
    }
}


.lrg-intro {
    @extend %h3;
}
.error-container.hidden {
        .error {
        clip: rect(0 0 0 0);
        clip: rect(0, 0, 0, 0);
        margin: -1px;
        padding: 0;
        overflow: hidden;
        position: absolute;
        width: 1px;
        height: 1px;
        }
}
.error-container {
   display: inline-block;
    .error {
        background: url('/Resources/images/sprites/alert.png') left center no-repeat;
        clip: initial;
        margin: -1px;
        padding: 0;
        overflow: hidden;
        position: absolute;
        height: 27px;
        text-indent: -200em;
        width: 30px;
    }
}
.hpc, #HoneyPot{
    display: none;
}