@charset "UTF-8";

/* Default Margin and Default Padding Variable*/
$base-size:         32px !default;
$margin-default:    $base-size;
$padding-default:   $base-size;
$height-default:    $base-size;
$width-default:     $base-size;

$height-promoblock-sml: $base-size * 8;
$height-promoblocks:    $base-size * 17;