/* Buttons*/

@mixin cta( $font-size, $min-width, $max-width, $padding-v, $padding-h, $border-radius) {

    border-radius: $border-radius;
    font-size: $font-size;
    font-weight: 700;
    min-width: $min-width;
    max-width: $max-width;
    padding: 0 $padding-h;
    border: 0;
    box-sizing: border-box;
    display: inline-block;
    font-weight: 700;
    line-height: $font-size + ( $padding-v * 2 ) + 2;
    min-width: $min-width;
    max-width: $max-width;
    word-wrap: break-word;
    min-height: 30px;
    text-align: center;
    text-decoration: none !important;
    text-transform: uppercase;
}

.pl-btn {

    @include media-breakpoint-up( sm ) {

        & + & { margin-left: 16px; } /* 20px - 4px for inline-block adjustment*/

    }

    &--red {

        background: $c-red;
        color: $c-white !important;

        &.disabled {
            cursor:default;
            opacity: 0.5;
        }

    }

    &--medium {

        @include cta(
            14px,
            130px,
            280px,
            10px,
            30px,
            3px
        );
    }

}