.ac {
	transition: all $t-short ease-in;
    overflow: hidden;
	&__title, h2.ac__title { 
		transition: all $t-medium ease-in;
		background: $c-purple-light; 
		padding: 15px 20px;
        box-sizing: border-box;
        width: 100%!important;  
		margin-bottom: 0px !important;
        color: $c-white;
        &:last-of-type { 
            margin-bottom: -15px !important;
        }
		&.active {
			background: $c-purple;  
            cursor: pointer;
		}
        &.shrink {
            transition: all $t-long ease-in;
            opacity: 0;
            height: 0;
            margin: 0;
            padding: 0;
        }
	}
    
	&__tab {
		max-height: 0;
		overflow: hidden;
        h4 {
            color: $c-purple;

        }
		&.active {
			/*transition: max-height $t-medium linear;*/
			max-height: 2000px;
		}
		@include media-breakpoint-up( md ) {
            transition: all $t-medium ease-out;
			
		}
	}
	&__content {
		padding: 32px;
        position: relative;
        transition: all $t-short ease-in;
        box-sizing: border-box;
	}
    &__cta-holder {
        text-align: center;
    }
    hr {
        border-bottom: 0;
        border-top: 1px solid #ececec;
        height: 0;
    }
	
}

.count {
    clear: both;
    &__title {
        float: left;
        max-width: 180px;
        p {
            padding: 5px 0;
            font-size: 1.6rem;
        }
        @include media-breakpoint-up( sm ) {
            max-width: none;
        }
        @include media-breakpoint-down(xs) {
            display: block;
            width: 100%;
        }
    }

    &__q {
        float: right;
        height:34px;
        background: $c-gray-lighter;
        border: 2px solid $c-purple;
        overflow: hidden;
        @include media-breakpoint-down(xs) {
            display: block;
            float: left;
            margin-bottom: 32px;
            margin-top: -7px;
        }
        @media (min-width: 437px) and (max-width: 543px) {
            margin-top: 0;
        }
    }
    &__input {
        display: inline-block;
        background: $c-white;
        margin-right: -4px;
        input.count__quantity {
            min-width: 9rem;
        }
        input.pl-input {
            width:50px;
            height:34px;
            text-align:center;
            border: none;
            font-size: 1.6rem;
            box-shadow: inset 0 0 0 0 $c-gray-light;
            padding: 0;
            &:focus {
                border:1px solid #e1e1e1;
                border: none;
            }
        }
    }
    &__plus, &__minus {
        display: inline-block;
        height: 34px;
        width: 34px;
        vertical-align: top;
        &.disabled {
            a {
                cursor: default;
                background: lighten($c-gray-lightest, 1%);
                color: $c-gray-light;
                opacity: 1;
                &:hover {
                    background: lighten($c-gray-lightest, 1%);
                    color: $c-gray-light;
                }
            }
        }
        a {
            transition: all 0.2s ease-in;
            height: 100%;
            width: 100%;
            display: block;
            padding: 5px 0;
            font-weight: 700;
            color: $c-purple;
            text-align:center;
            text-decoration: none;
            background: $c-white;
            &:hover {
                color: darken($c-gray-light, 5%);
                background: darken($c-gray-lighter, 5%);
            }
        }
    }
    &__minus {
        margin-right: -4px;
        a {
            padding: 4px 0;
        }
    }
}

.advance-form {
    background: $c-white;
}

h3.result, h4.result {
    color: $c-gray-darker;
}

.result-image {
    min-height: 140px;
    position: relative;
    img {
        margin-left: auto;
        margin-right: auto;
    }
    @include media-breakpoint-up( md ) {
        display: table;
        margin: 0 auto;
        padding-bottom: 30px;
    }

}

.result-image-container {
    @include media-breakpoint-up( md ) {
        display: table-cell;
        vertical-align: bottom;
    }
}
.result-marker {
    transition: all $t-long ease-in-out;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    display: block;
    background: $c-white;
    opacity: 0.8;
}

#ac-results {
    z-index: 10;
}
.ac-results {
    background: $c-white;
    &--step-1 {
        position: relative;
        z-index: 1;
        background: $c-white;
        &.bounceOutRight {
            z-index: -1;
        }
        .result-block {
            @include media-breakpoint-up(md) {
                display: inline-block;
                float: left;
                width: 50%;
            }
        }
    }
    &--step-2 {
        transition: all $t-medium ease-out;
        position: absolute;
        z-index: -1;
        top: 0;
        left: 0;
        width: 100%;
        background: $c-white;
        border-bottom: 1px solid $c-gray-gt;
        .gr:first-of-type {
            padding: 0;
        }
        h2 {
            color: $c-gray-darker;
        }
        &.bounceInLeft {
            z-index: 3; 
        }
        
        
    }
    &__close {
        position: absolute;
        right: 48px;
        top: 32px;
        @media (max-width: 767px) {
            top: 20px;
            right: 34px;
        }
    }
}
.caveat {
    transition: all $t-medium ease-out;
    background: $c-white;
    p{
        padding-top: 30px;
    }
    &.locked {
        position: absolute;
        bottom: 0;
        hr {
            display: none;
        }
    }
}
.ac__results--small {
    @include media-breakpoint-down(md) {
        display: none;
    }
}
.ac__contact-fields {
    background: $c-gray-gt;
    @include media-breakpoint-down( sm ) {
        &.gmp2 {
            margin: 0;
        }
    }
}

.h1Cont.banner-off-400 {
    .banner-img {
        right: -250px;
        top: -10px;
    }
    @include media-breakpoint-down( sm ) {
        .banner-img {
            top: 0;
            right: -420px;
            background-size: 15%;
        }
    }
}