.pl-btn, .pl-btn--medium {
    @extend %btn-primary;
    border-radius: 0;
    font-weight: 400;
    line-height: inherit;
    font-family: inherit;
    padding: 1rem 3rem;
    text-transform: inherit;
    color: $color-alizarin !important;
    @include hover {
        color: $color-light !important;
        @include pointer-coarse{
            color: $color-alizarin !important;
        }
    }
}
.pl-btn.disabled {
    @extend %btn-primary;
    &:after {
        content: ""; 
        display: block;
        position: absolute;
        z-index: -1;
        top: 0;
        right: 0;
        height: 100%;
        width: 0;
        overflow: hidden; 
        background: $color-alizarin;
        box-sizing: border-box;
        @include transition(width, .2s);
    }
    span {
        display: none;
    }
    &:hover {
        color: $color-alizarin !important; 
        @include pointer-coarse{
            color: $btnPrimaryColor !important;
        }
        span {
            display: none !important;
        }
    }
}
.rev-text.pl-input.collection.collection-element.collection-group {
    color: inherit;
    background: none;
    -webkit-appearance:none;
    -moz-appearance: none;                    
    padding: 1rem 4rem 1rem 2rem;        
    border: 0.1rem solid $color-minsk;
    border-radius: 0;
    margin-bottom: ($margin-default * 0.5);
    width: 100%;
    font-size: $size-regular;
    line-height: inherit !important;
}
.styleSelBox.half-width.collection.collection-element {
    color: $color-minsk;
    background: none;
    -webkit-appearance:none;
    -moz-appearance: none;                    
    padding: 1rem 4rem 1rem 2rem;        
    border: 0.1rem solid $color-minsk;
    border-radius: 0;
    margin-bottom: ($margin-default / 2);
    width: 50%;
    font-size: $size-regular;
    @include media-only-mobile {
        max-width: 220px;
    }
}
.pl-checkbox {
    position: relative !important;
    input[type="checkbox"]+label:before {
        margin-left: 0;
        border-radius: 0;
    }
    input[type="checkbox"]+label {
        position: relative !important;
        &.form-confirm {
            margin-right: ($margin-default * 0.5);
        }
    }
    input:checked+label:before {
        background: #fff !important;
        border-radius: 0;
    }
    input[type="checkbox"]+label:after {
        content: '';    
        width: 1.4rem;
        height: 0.75rem;
        position: absolute;
        left: 27px;
        top: 4px;
        margin-top: 0.4rem;
        border: 0.2rem solid $color-minsk;
        border-top: none;
        border-right: none;
        background: transparent;
        opacity: 1;            
        -ms-transform: rotate(-55deg) skewX(-15deg);            
        transform: rotate(-55deg) skewX(-15deg);
    }
    .pl-checkbox input:checked+label:before {
        background:transparent !important;
    }
}
.pl-list {
    margin-top: 0;
    margin-bottom: 0;
}
.collection {
    &:after {
        content: '';   
        display: inline-block;                     
        border-left: 4px solid transparent;
        border-right: 4px solid transparent;
        border-top: 7px solid $color-minsk;
        pointer-events: none;          
        margin-left: -3rem;        
    }
    &.ac__contact-fields, &.ac__content, &.ac-results--step-1, &.ac__ignore-dropdown, &#ac-location {
        &:after {
            display: none;
        }
    }
}
.ac__contact-details.pl-spacer.pl-spacer--in--small {
    .lrg-intro, h2 {
        @extend %h4;
        color: $color-dark;
    }
    input[type="text"] {
        border: 0.1rem solid $color-minsk;    
        background: $color-light;
        width: ($margin-default * 7);
        margin-right: ($margin-default / 2);
        color: $color-dark;
            &:disabled {
            color: $color-light;
            opacity: 0.45;
        }
        &.errors {
            border-color: $color-alizarin;
        }
    }
    input[type="email"] {
        color: $color-minsk;
        background: $color-light;
        -webkit-appearance:none;
        -moz-appearance: none;                       
        padding: 10px;
        border: 0.1rem solid $color-minsk;
        border-radius: 0;
        margin-bottom: ($margin-default / 2);
        margin-right: ($margin-default / 2);
        width: $margin-default * 7;
        font-size: $size-regular;
        color: $color-dark;
        &:disabled {
            color: $color-light;
            opacity: 0.45;
        }
        &.errors {
            border-color: $color-alizarin;
        }
    }
    label {
        &.errors {
            &:before {
                border-color: $color-alizarin;
                border-width: 2px;
            }
        }
    }
    .submit.submit-element {
        @extend %btn-primary;
        @include hover {
            background: $color-alizarin !important; 
            color: $color-light;
        }
        @include pointer-coarse{
            color: $btnPrimaryColor !important;
            background: transparent !important;
        }
    }
    input#contact-email {
        &:before {
            background: $color-light;
        }
    }
}
#ac-revenue {
    .grid.g24.pl-spacer.pl-spacer--in--smaller.pl-spacer--cut-h {
        .gr.clear.ac__ignore-dropdown.collection.collection-container {
            .grid.g8 {
                @media (min-width: 1024px) {
                    width: 33.333333%;
                    float: left;
                }
                @media (max-width: 1023px) {
                    width: 50%;
                    float: left;
                }
                @media (max-width: 767px) {
                    width: 100%;
                }

                .pl-list {
                    &:nth-child(3) {
                        margin-bottom: 15px;
                    }
                }
            }
        }
    }
}
.b, strong {
    font-family: "GT-Walsheim-Pro-Bold",Arial,Helvetica,sans-serif;
}
.count__q {
    border: 0.1rem solid $color-minsk;
}
input[type="checkbox"] {
    left: -999em;
}

.pl-checkbox {
    input[type="checkbox"]:focus + label {
        &:before {
        border: 0.1em solid $color-minsk !important;
        }
        color: $color-minsk;
    }
}

.ac__tab.animated {
    border-bottom: 1px solid $color-light;
    &:last-child {
        border-bottom: 0;
    }
}

input[type="text"], textarea, .form-input {
    font-size: $size-regular;
}

input#firstName, input#lastName, input#companyName, input#prefContactEmail, input#prefContactPhone  {
    width: 240px;
}

input#prefContactPhone {
    padding: 10px;
}

.ac__content {
    @include media-only-mobile {
        padding-left: 16px !important;
        padding-right: 16px !important;
    }
}

.ac__contact-details.pl-spacer.pl-spacer--in--small {
    @include media-only-mobile {
        padding-left: 16px !important;
        padding-right: 16px !important;
    }
}