*, *:before, *:after {
	box-sizing: border-box;
}
.stuck {
	z-index: 9999;
	position: fixed;
}
.hide-text {
	display: inline-block;
	text-indent: -999em;
	white-space: nowrap;
}
.hide {
	display: none;
}
.show {
	display: block;
}
.ir {
    background-color: transparent;
    border: 0;
    overflow: hidden;
}
.ir:before {
    content: "";
    display: block;
    width: 0;
    height: 150%;
}
