/* Margins*/

.pl-rm {

	&--right { margin-right: 0; }
	&--left { margin-left: 0; }
	&--top { margin-top: 0; }
	&--bottom { margin-bottom: 0; }

}

/* Block model related utilities (spacing, sizing, etc.).*/

.pl-push {

	@include media-breakpoint-down( sm ) {

		&--mobile {

			&--large:after {

				@include space-large( margin-bottom );

				content: '';
				display: block;
				margin-top: -1px;

				width: 100%;
				height: 1px;

			}

			&--medium:after {

				@include space-medium( margin-bottom );

				content: '';
				display: block;
				margin-top: -1px;

				width: 100%;
				height: 1px;

			}

			&--small:after {

				@include space-small( margin-bottom );

				content: '';
				display: block;
				margin-top: -1px;

				width: 100%;
				height: 1px;

			}

			&--smaller:after {

				@include space-smaller( margin-bottom );

				content: '';
				display: block;
				margin-top: -1px;

				width: 100%;
				height: 1px;

			}

			&--smallest:after {

				@include space-smallest( margin-bottom );

				content: '';
				display: block;
				margin-top: -1px;

				width: 100%;
				height: 1px;

			}

		}

	}

	@include media-breakpoint-only( md ) {

		&--tablet {

			&--large:after {

				@include space-large( margin-bottom );

				content: '';
				display: block;
				margin-top: -1px;

				width: 100%;
				height: 1px;

			}

			&--medium:after {

				@include space-medium( margin-bottom );

				content: '';
				display: block;
				margin-top: -1px;

				width: 100%;
				height: 1px;

			}

			&--small:after {

				@include space-small( margin-bottom );

				content: '';
				display: block;
				margin-top: -1px;

				width: 100%;
				height: 1px;

			}

			&--smaller:after {

				@include space-smaller( margin-bottom );

				content: '';
				display: block;
				margin-top: -1px;

				width: 100%;
				height: 1px;

			}

			&--smallest:after {

				@include space-smallest( margin-bottom );

				content: '';
				display: block;
				margin-top: -1px;

				width: 100%;
				height: 1px;

			}

		}

	}

	@include media-breakpoint-up( lg ) {

		&--desktop {

			&--large:after {

				@include space-large( margin-bottom );

				content: '';
				display: block;
				margin-top: -1px;

				width: 100%;
				height: 1px;

			}

			&--medium:after {

				@include space-medium( margin-bottom );

				content: '';
				display: block;
				margin-top: -1px;

				width: 100%;
				height: 1px;

			}

			&--small:after {

				@include space-small( margin-bottom );

				content: '';
				display: block;
				margin-top: -1px;

				width: 100%;
				height: 1px;

			}

			&--smaller:after {

				@include space-smaller( margin-bottom );

				content: '';
				display: block;
				margin-top: -1px;

				width: 100%;
				height: 1px;

			}

			&--smallest:after {

				@include space-smallest( margin-bottom );

				content: '';
				display: block;
				margin-top: -1px;

				width: 100%;
				height: 1px;

			}

		}

	}

}

.pl-spacer {

	&--in {

		&--large { @include space-large( padding ); }
		&--medium { @include space-medium( padding ); }
		&--small { @include space-small( padding ); }
		&--smaller { @include space-smaller( padding ); }
		&--smallest { @include space-smallest( padding ); }
		&--smallest-c { @include space-smallest-c( padding ); }
		&--x-smallest { @include space-x-smallest( padding ); }

	}

	&--out {

		&--large { @include space-large( margin ); }
		&--medium { @include space-medium( margin ); }
		&--small { @include space-small( margin ); }
		&--smaller { @include space-smaller( margin ); }
		&--smallest { @include space-smallest( margin ); }
		&--smallest-c { @include space-smallest-c( margin ); }
		&--x-smallest { @include space-x-smallest( margin ); }

	}

	&--cut-h {

		&[class*="in"] { padding-right: 0; padding-left: 0; }
		&[class*="out"] { margin-right: 0; margin-left: 0; }

	}

	&--cut-v {

		&[class*="in"] { padding-top: 0; padding-bottom: 0; }
		&[class*="out"] { margin-top: 0; margin-bottom: 0; }

	}

	&--cut-top {

		&[class*="in"] { padding-top: 0; }
		&[class*="out"] { margin-top: 0; }

	}

	&--cut-right {

		&[class*="in"] { padding-right: 0; }
		&[class*="out"] { margin-right: 0; }

	}

	&--cut-bottom {

		&[class*="in"] { padding-bottom: 0; }
		&[class*="out"] { margin-bottom: 0; }

	}

	&--cut-left {

		&[class*="in"] { padding-left: 0; }
		&[class*="out"] { margin-left: 0; }

	}

}