/* ================================= 2 FONT SIZING ================================= */
html{
    font-size:62.5%;
    -webkit-font-smoothing:antialiased;
}
@media only screen and( -webkit-min-device-pixel-ratio: 1.25 ),
       only screen and( min-resolution: 120dpi ),
       only screen and( min-resolution: 1.25dppx ) {

    body {
        @include font-size(10);
    }
} 
body {
    @include font-size(10);
    font-family: $font-Regular;
    font-weight: 400;
    line-height:1.4;
}

%h1,h1,.h1 {
    font-size: $size-gigantic;
    line-height: 80px;
    font-weight: 300;
    margin: 0 0 15px 0;

    @include media-only-mobile {
        font-size: $size-big;
        line-height: 56px;
    }
}
%h2,h2,.h2{
    font-size: $size-big;
    line-height: 56px;
    font-weight: 300;
    margin: 0 0 15px 0;

    @include media-only-mobile {
        font-size: $size-xx-large;
        line-height: 40px;
    }
}
%h3,h3,.h3,.big-text{
    font-size: $size-x-large;
    line-height: 32px;
    font-weight: 300;
    margin: 0 0 15px 0;
}
%h4,h4,.h4{
    font-size: $size-large;
    line-height: 1.5em;
    font-weight: 300;
    margin: 0 0 15px 0;
}

%p,p {
    font-size: $size-regular;
    line-height: 150%;
    margin: 0 0 15px 0;
}
%li,li{
    font-size: $size-regular;
    line-height: 150%;
}
.bullet-list{
    li{
        margin-bottom: 1em;
    }
}
%p-big,.p-big{
    font-size: $size-large;
    line-height: 200%;
    font-weight: 400;
    margin: 0 0 15px 0;
}
%p-medium, %p-small, .p-medium, .small-text{
    font-size: $size-large;
    line-height: 150%;
    font-weight: 400;
    margin: 0 0 15px 0;
}
%p-small, .p-small, .small-text{
    font-size: $size-tiny;
}
%p-tag,.p-tag, .sectionTag{
    font-size: $size-tiny;
    line-height: 100%;
    font-weight: 400;
    letter-spacing: 0.2em;
    margin: 0 0 15px 0;
    a{
        color: inherit;
        text-decoration: none;
        @include hover{
            color: $color-alizarin;
        }
    }
}
%richtext-block,.richtext-block {
/*    margin-top: -$margin-default;*/
    h1 {
        @extend %h3;
        margin-top: -0.75 * $margin-default;
        padding-bottom: 2 * $padding-default;
        line-height: 180%;
    }
    p {
        line-height: 200%;
    }
}
.center-text {
    text-align:center;
}
/* Anchors*/
/* a{text-decoration:underline;*/
/*     color:$linkColor;*/
/*     @include hover{*/
/*         color: $linkColorHover; */
/*     }*/
/* }*/
a {
    text-decoration: none;
    color: $color-minsk;
    @include hover {
        color: $color-alizarin;
    }
}