.text {

	&-left { text-align: left; }
	&-right { text-align: right; }
	&-center { text-align: center; }
	&-justify { text-align: justify; }
  &-small { 
    font-size: 1.3em;  
  }
  &-italic {
    font-style: italic;
  }

}

/* Text alignment*/

.text-left--mobile { @include media-breakpoint-down( sm ) { text-align: left; } }
.text-left--tablet { @include media-breakpoint-only( md ) { text-align: left; } }
.text-left--desktop { @include media-breakpoint-up( lg ) { text-align: left; } }

.text-right--mobile { @include media-breakpoint-down( sm ) { text-align: right; } }
.text-right--tablet { @include media-breakpoint-only( md ) { text-align: right; } }
.text-right--desktop { @include media-breakpoint-up( lg ) { text-align: right; } }

.text-justify--mobile { @include media-breakpoint-down( sm ) { text-align: justify; } }
.text-justify--tablet { @include media-breakpoint-only( md ) { text-align: justify; } }
.text-justify--desktop { @include media-breakpoint-up( lg ) { text-align: justify; } }

.text-center--mobile { @include media-breakpoint-down( sm ) { text-align: center; } }
.text-center--tablet { @include media-breakpoint-only( md ) { text-align: center; } }
.text-center--desktop { @include media-breakpoint-up( lg ) { text-align: center; } }

.serif {
    font-family: Georgia,Times,serif;
    font-size: 2rem;
}

/**/
/* Responsive utilities*/
/**/

@each $bp in map-keys($grid-breakpoints) {
  .hidden-#{$bp}-up {
    @include media-breakpoint-up($bp) {
      display: none !important;
    }
  }
  .hidden-#{$bp}-down {
    @include media-breakpoint-down($bp) {
      display: none !important;
    }
  }
}