html, body {
    background: $color-warm-grey;
    /* overflow-x: hidden;*/
    /* width: 100%;*/
    /* max-width: 100%;*/
    direction: ltr;
    /* height: auto;*/
    .overflowHidden {
        overflow: hidden;
    }
}

.main {
    background: $color-warm-grey;
}

img {
    display:block;
    margin:0;
    padding:0;
    max-width:100%;
    height:auto;
}

%clear, .clear{
    &:after{
        content: "";
        display: table;
        clear: both;
    }
}
%hidden, .hidden{
    display: none;
}

%hide-on-mobile, .hide-on-mobile {
    @include media-only-mobile {
        display: none !important;
    }
}

hr.divider{
    margin: $margin-default 0;
    border: 0;
    border-top: 1px solid $color-rolling;
}

.op-0{
    opacity: 0;
}
