%box-base-white,
.box-base-white {
    position: relative;
    margin-top: $margin-default * .75;
    margin-bottom: $margin-default;
    padding: $margin-default * .75 $margin-default * .75 $margin-default;
    box-sizing: border-box;
    background-color: $color-light;
    background-position: right bottom;
    background-repeat: no-repeat;
    a {
        display: inline-block;
        max-width: 100%;
        overflow: hidden;
        text-overflow: ellipsis;
        text-decoration: none;
        vertical-align: bottom;
        width: auto;
    }
    .expert {
        @extend %h3;
    }
    &.solid-b {
        background-color: $color-alizarin;
        color: $color-light;
        a {
            color: inherit;
            @include hover {
                text-decoration: underline;
            }
        }
    }
}

%sml-cont,
%med-cont,
%lrg-cont {
    @extend %box-base-white;
    max-height: $height-promoblocks;
    margin: 0 0 $margin-default 0;
}

%sml-cont {
    min-height: $height-promoblock-sml;
}

%med-cont,
%lrg-cont {
    min-height: $height-promoblocks;
}

%overlay,
.overlay {
    background: rgba(0, 0, 0, 0.7);
    height: 100%;
    left: 0;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 80;
}

%yt-container,
.youtube-container {
    padding-top: 0;
    position: absolute;
    z-index: 101;
}

%filter-bar {
    @extend %clear;
    margin-top: 0;
    background: $color-minsk;
    color: $color-light;
    /*padding: $padding-default 0;*/
    overflow: hidden;
    h2 {
        display: inline-block;
        font-size: $size-x-large;
        line-height: 2em;
        vertical-align: top;
        margin: 0 $margin-default 0 0;
        @include rtl {
            margin: 0 0 0 $margin-default;
        }
    }

   .single-col {
        padding: $padding-default 0;
   }

   .left-col {
        padding-top: $padding-default;
        margin-bottom: -$margin-default / 2;
    }
    .right-col {
        float:right;        
        padding-top: $padding-default;
        margin-bottom: $margin-default;

        @include media-only-mobile {
            float:none;
        }
    }   

    @include media-from-tablet {
        input, button {
            margin: 0;
        }
    }

   [class*="col"] {
        div {
            display: inline-block
        }

        @include media-only-mobile {
            div,button {
                display: block;
            }

            .form-select select {
                width: 100% ;
            }
           
            input {
                margin-bottom: $margin-default / 2;
            }
           
        }
        @include media-from-tablet {
            button, a {
                margin: 0;
            }
        }
    } 

    .define-search {
        display: inline-block;
        
        .js-define-search {
            display: inline-block;
            li {
                display: inline-block;
                a {
                    text-decoration: none;
                    color: $color-light;
                    font-size: $size-regular;
                    font-weight: 700;    
                    padding: 0 $padding-default;

                    &.active {
                        color: $color-alizarin;
                    }
                }

            }
        }
    }
}

%filter-bar-wrapper,
.filter-bar-wrapper {
    display: block;
    background: $color-jacarta;
    color: $color-light;
}

%filters-container,
.filters-container {

    ul {
        list-style: none;
    }
    
    padding: $padding-default 0 $padding-default / 2;
    a {
        display: inline-block;
        color: $color-light;
        text-decoration: none;
        font-size: $size-large;
        margin-bottom: $margin-default / 2;

    }
}

%topSpacingContainer,
.topSpacingContainer,
.relatedContainer{
    margin-top: $margin-default;
}