/*breakpoints*/
$grid-breakpoints: (
  // Extra small screen / phone
  xs: 0,
  // Small screen / phone
  sm: 544px,
  // Medium screen / tablet
  md: 768px,
  // Large screen / desktop
  lg: 992px,
  // Extra large screen / wide desktop
  xl: 1200px
) !default;

/* Paths*/

$baseImagePath:		'../images';

/* Spacing*/

$spacer-l: 120px;
$spacer-m: 80px;
$spacer-s: 65px;
$spacer-xs: 30px;
$spacer-xxs: 20px;
$spacer-xxs-c: 25px;
$spacer-xxxs: 15px;

$spacer-l-m: 80px;
$spacer-m-m: 60px;
$spacer-s-m: 35px;
$spacer-xs-m: 20px;
$spacer-xxs-m: 20px;
$spacer-xxs-m-c: 20px;
$spacer-xxxs-m: 15px;

/* Transition lengths*/

$t-short: 0.2s;
$t-medium: 0.6s;
$t-long: 1s;

/* Opacity*/

$o-max: 0.5;
$o-medium: 0.25;	
$o-normal: 0.15;	
$o-min: 0.05;