%btn-primary, [class*="btn-primary"] {
    font-weight: 400;
    font-size: $size-regular;
    color: $btnPrimaryColor;
    background: transparent;
    border: 2px solid $btnPrimaryColor;
    padding: 1rem 3 * 1rem;
    display: inline-block;
    text-align: center;
    margin: 2rem 0;
    text-decoration: none;
    position: relative;
    overflow: hidden;
    box-sizing: border-box;
    z-index: 1;
    @include transition(all, .2s);
    @include media-small-mobile {
        width: 100%;
    }
    &:after{
        content: ""; 
        display: block;
        position: absolute;
        z-index: -1;
        top: 0;
        /*left: 0;*/
        right: 0;
        height: 100%;
        width: 0;
        overflow: hidden; 
        /*transform: translate(100%, 0);*/
        /*transform: translate3d(100%, 0, 0);*/
        background: $color-alizarin;
        box-sizing: border-box;
        @include transition(width, .2s);
        pointer-events: none;
        @include pointer-coarse{
            display: none;
        }
    }
    
    span{
        display: none;
    }
    /*span {
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        height: 100%;
        width: 100%;
        overflow: hidden; 
        transform: translate(100%, 0);
        transform: translate3d(100%, 0, 0);
        background: $color-alizarin;
        box-sizing: border-box;
        @include transition(transform);
        &:after {
            content: attr(data-content);
            display: block;
            position: relative;
            overflow: hidden;
            top: 0;
            left: 0;
            right: 0;
            height: 100%;
            color: $color-light;
            width: 100%;
            padding: 1rem 0;
            box-sizing: border-box;
            white-space: nowrap;
            transform: translate(-100%, 0);
            transform: translate3d(-100%, 0, 0);
            @include transition(transform);
        }
    }*/
    @include hover {
        /*color: $btnPrimaryColor;
        span {
            transform: translate(0, 0);
            transform: translate3d(0, 0, 0);
            &:after {
                transform: translate(0, 0);
                transform: translate3d(0, 0, 0);
            }
        }*/
        color: $color-light;
        @include pointer-coarse{
            color: $btnPrimaryColor;
        }
        &:after { 
            width: 100%;
            /*transform: translate3d(0, 0, 0) scale(1.1);*/
        }
    }
}

%btn-primary-light, .btn-primary-light {
    color: $btnPrimaryLightColor;
    border-color: $btnPrimaryLightColor;
    &:after{
        background: $color-light;
    }
    /*span {
        background: $color-light;
        &:after {
            color: $color-minsk;
        }
    }*/
    @include hover {
        color: $color-minsk;
        @include pointer-coarse{
            color: $btnPrimaryLightColor;
        }
    }
}
.btn-primary-alt {
    color: $color-light;
    &:before{
        background: $color-light;
        content: "";
        display: block;
        position: absolute;
        top: 0;
        left:0;
        width: 100%;
        height: 100%;
        z-index: -1;        
    }
    @include pointer-coarse{
        background: $color-alizarin;
        &:before{
            display: none;
        }
    }
    &:after{
        /*transform: translate(0, 0) scale(1.1);*/
        /*transform: translate3d(0, 0, 0) scale(1.1);*/
        width: 100%;
    }
    @include hover {
        color: $color-alizarin;
        @include pointer-coarse{
            color: $color-light;
        }
        &:after {
            width: 0;
            /*transform: translate(100%, 0);*/
            /*transform: translate3d(100%, 0, 0);*/
        }
    }
    /*span {
        transform: translate(0, 0);
        transform: translate3d(0, 0, 0);
        &:after {
            transform: translate(0, 0);
            transform: translate3d(0, 0, 0);
        }
    }
    @include hover {
        background: $color-light;
        span {
            transform: translate(100%, 0);
            transform: translate3d(100%, 0, 0);
            &:after {
                transform: translate(-100%, 0);
                transform: translate3d(-100%, 0, 0);
            }
        }
    }*/
}

%btn-secondary, 
.btn-secondary, 
[class*="btn-secondary"] {
    font-weight: 400;
    color: $btnSecondaryColor;
    font-size: $size-regular;
    padding: .5rem ($margin-default + 10px) .5rem 0;
    display: inline-block;
    text-align: center;
    text-decoration: none;
    position: relative;
    &:after {
        @extend %icon;
        @extend %icon-cta-arrow;
        @include transition(transform);
        position: absolute;
        top: 50%;
        right: 0;
        -ms-transform: translate(-10px, -50%);
        transform: translate(-10px, -50%);
        @include rtl{
            /*rtl:ignore*/
            -ms-transform: translate(10px, -50%) rotate(-180deg);
            transform: translate(10px, -50%) rotate(-180deg);
        }
    }

    @include hover {
        &:after {
            -ms-transform: translate(0, -50%);
            transform: translate(0, -50%);
            @include rtl{
                /*rtl:ignore*/
                transform: translate3d(0, -50%) rotate(-180deg);
            }
        }
    }

}

%btn-secondary-light, .btn-secondary-light {
    color: $color-light;
    margin-bottom: $margin-default / 2;
    @include hover {
    color: $color-alizarin;
    }
}

%chips,.chips {    

    display: inline-block;
    color: $color-jacarta;
    padding: 0.75rem 3rem;
    font-size: $size-tiny;
    text-decoration: none;
    border: 0.1rem solid $color-bison;
    border-radius: 0.5rem;

    @include hover{
        color: $color-light;
        background-color: $color-jacarta;
        border-color: $color-jacarta;
    }
}

%chips-with-close, .chips-with-close {

    position: relative;
    padding-right: 4rem;
    padding-left: 1.5rem;
    &:before,
    &:after {
        content: '';
        position: absolute;
        height: 0.1rem;
        width: 1.5rem;  
        background: $color-jacarta;
        top:2rem;
        right: 1rem;                       
    }

    &:after {
        -ms-transform: rotate(-45deg);
        transform: rotate(-45deg);
    }

    &:before {
        
        -ms-transform: rotate(45deg);
        
        transform: rotate(45deg);
    }

    @include hover{

        &:before,
        &:after {
            background: $color-light;
        }
    }    
}