.pl-opacity {

	&--max { opacity: $o-max; }
	&--medium { opacity: $o-medium; }
	&--normal { opacity: $o-normal; }
	&--min { opacity: $o-min; }

}

.pl-bg {

	&--purple { background-color: $c-purple; }
	&--purple-light { background-color: $c-purple-light; }
	&--red { background-color: $c-red; }
	&--red-light { background-color: $c-red-light; }

	&--black { background-color: $c-black; }
	&--white { background-color: $c-white; }

	&--gray-darkest { background-color: $c-gray-darkest; }
	&--gray-darker { background-color: $c-gray-darker; }
	&--gray-dark { background-color: $c-gray-dark; }
	&--gray { background-color: $c-gray; }
	&--gray-light { background-color: $c-gray-light; }
	&--gray-lighter { background-color: $c-gray-lighter; }
	&--gray-lightest { background-color: $c-gray-lightest; }

}

.pl-color {

	&--purple { color: $c-purple; }
	&--red { color: $c-red; }

	&--black { color: $c-black; }
	&--white { color: $c-white; }

	&--gray-darkest { color: $c-gray-darkest; }
	&--gray-darker { color: $c-gray-darker; }
	&--gray-dark { color: $c-gray-dark; }
	&--gray { color: $c-gray; }
	&--gray-light { color: $c-gray-light; }
	&--gray-lighter { color: $c-gray-lighter; }
	&--gray-lightest { color: $c-gray-lightest; }

}